import React, { Component } from 'react';
import styled from 'styled-components';



const ProfileWrapper = styled.div`
  width: 50%;
  margin: 0px;
`;



const NameTitle = styled.h3`
  color:#000;
  font-size:14px;
  color:white;
  margin-left:10px;
  margin-top:30px;
`
class Name extends Component{
    constructor() {
        super ();
        this.state = {
          data: {},
          repositories: [],
          loading: true,
          error: '',
        };
      }
    
      async componentDidMount() {
        try {
          const profile = await fetch('https://api.github.com/users/octocat');
          const profileJSON = await profile.json();
    
          if (profileJSON) {
    
            this.setState({
              data: profileJSON,
              
              loading: false,
            });
          }
        } catch (error) {
          this.setState({
            loading: false,
            error: error.message,
          });
        }
      }
    
      render() {
        const {data,loading, error } = this.state;
        if (loading || error) {
          return <div>{loading ? 'Loading...' : error}</div>;
        }
        return (
          <ProfileWrapper>
           <NameTitle>{ data.name} xin chao</NameTitle>
          </ProfileWrapper>
        );
      }
}
export default Name;